        viewer3DImpl.api.addEventListener(av.SETTINGS_PANEL_CREATED_EVENT, hideShadows);
        var resize = function(ev) {
            _occlusionCulling.onResize();
        };
        viewer3DImpl.api.addEventListener(av.VIEWER_RESIZE_EVENT, resize);

